import React, { FC, useEffect, useRef } from "react";
import { PolyIconProps } from "./Polygon.types";
import { Svg, Path } from "../Svg";
import { useNgon } from "./Polygon.methods";
import { rad } from "../../geometry";

export const PolyIcon: FC<PolyIconProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fill = '#000',
	stroke = '#000',
	strokeWidth = 0.1,
	icon
}) => {
	const [points, viewBox, scaledStrokeWidth] = useNgon(sides, scale, rad(rotation), cornerRadius, strokeWidth);
	const ref = useRef(null);
	useEffect(()=>{
		const elems = (ref.current! as SVGGElement).children
		if (elems.length === 0 || elems[0].tagName !== 'svg') return; //do nothing because I wasnt trained for this
		const elem = elems[0];
	})
	return (<Svg 
	viewBox={viewBox}
	width='1rem'
	height='1rem'>
		<Path {...{points, fill, stroke, strokeWidth:scaledStrokeWidth}}/>
		<g ref={ref}>
			{icon}
		</g>
	</Svg>)
}